import { ErrorMessage, Field, Form, Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { ModalBody, ModalHeader, ModalFooter } from "reactstrap";
import { toast } from 'react-toastify';
import TextError from '../../helpers/FormikHelper/TextError';
import userServices from '../../services/user.services';
import { FieldSchema } from '../../schema/FieldSchema';
import FoodSelect from '../Stays/FoodSelected';
import FacilitiesSelect from '../Stays/FacilitiesSelected';
import IncludeSelect from './IncludesSelected';
import WhatToBringSelect from './WhatToBringSelect';

const EventsForm = ({ toggle, isAddMode, selectedRow, getData }) => {
    const [includes, setIncludes] = useState([])
    const [carry, setCarry] = useState([])
    const [expTyes, setExpTypes] = useState([])
    const [facilities, setFacilites] = useState([])

    const initialValues = {
        experienceName: '',
        experienceTypeId: ''
    }

    const savedValues = {
        experienceName: selectedRow?.experienceName,
        experienceTypeId: selectedRow?.experienceTypeId,
        durationName: selectedRow?.durationName,
        hostName: selectedRow?.hostName,
        startDate: selectedRow?.startDate,
        includes: selectedRow?.includes,
        carry: selectedRow?.carry,
        busStation: selectedRow?.busStation,
        railwayStation: selectedRow?.railwayStation,
        airport: selectedRow?.airport,
        priceRegular: selectedRow?.priceRegular,
        isExperience: selectedRow?.isExperience,
        experience_status: selectedRow?.experience_status
    }

    const onSubmit = async (values) => {

        if (isAddMode === 'Add') {
            let result = await userServices.addPolicies(values);
            if (result.data.status === 'success') {

                toggle()
                getData()
                toast.success('Data Added successfully', { toastId: 'registration_success', autoClose: 3000 });
            } else {
                toast.error(result.data.message, { toastId: 'registration_fail', autoClose: 3000 });
            }
        } else {
            values.id = selectedRow?._id
            values.startDate = new Date(selectedRow?.startDate)
            const result = await userServices.updateEventAndExpById(values);
            if (result.data.status === 'success') {

                toggle()
                getData()
                toast.success('Data Updated successfully', { toastId: 'update_success', autoClose: 3000 });
            } else {
                toast.error(result.data.message, { toastId: 'update_fail', autoClose: 3000 });

            }
        }
    }

    useEffect(() => {
        userServices.expIncludesList().then((result) => {
            if (result.data.status === 'success') {
                setIncludes(result.data.data)
            }
        })

        userServices.expTypeList().then((result) => {
            if (result.data.status === 'success') {
                setExpTypes(result.data.data)
            }
        })

        userServices.carryList().then((result) => {
            if (result.data.status === 'success') {
                setCarry(result.data.data)
            }
        })
    }, [])

    return (
        <React.Fragment>
            <Formik
                initialValues={isAddMode === 'Add' ? initialValues : savedValues}
                // validationSchema={FieldSchema(`${isAddMode === 'Add' ? 'ADD_EVENTS' : 'ADD_EVENTS'}`)}
                onSubmit={onSubmit}
                enableReinitialize
            >
                {({ setFieldValue, values }) => (<Form>
                    <ModalHeader toggle={toggle} className='border-0 pb-0'>
                        <div className="card-title d-flex align-items-center mb-0">
                            <h5 className="mb-0 text-primary">{isAddMode === 'Add' ? 'Add Event' : 'Update Event'}</h5>
                        </div>
                    </ModalHeader>
                    <ModalBody className="">
                        <div className="row" >
                            <div className="col-12 mb-3">
                                <label htmlFor="experienceName" className="form-label">
                                    Experience Name
                                </label>
                                <Field type="text" className="form-control" id="rule" name='experienceName' disabled />
                                <ErrorMessage name='experienceName' component={TextError} />
                            </div>
                            <div className="col-md-6 mb-3">
                                <label htmlFor="experienceTypeId" className="form-label">
                                    Experience Types
                                </label>
                                <Field as='select' className='form-select' id="experienceTypeId" name="experienceTypeId" >
                                    <option disabled value="">Select Experience Types</option>

                                    {
                                        expTyes.map((e, key) => {
                                            return <option key={key} value={e._id}>{e.experienceName}</option>;
                                        })
                                    }
                                </Field>
                                <ErrorMessage name="stayTypeId" component={TextError} />
                            </div>

                            <div className="col-6 mb-3">
                                <label htmlFor="durationName" className="form-label">
                                Duration
                                </label>
                                <Field type="text" className="form-control" id="rule" name='durationName' disabled />
                                <ErrorMessage name='durationName' component={TextError} />
                            </div>
                            <div className="col-12 mb-3">
                                <label htmlFor="startDate" className="form-label">
                                    Event Date
                                </label>
                                <Field type="text" className="form-control" id="rule" name='startDate' disabled/>
                                <ErrorMessage name='startDate' component={TextError} />
                            </div>
                            <div className="col-6 mb-3">
                                <label htmlFor="hostName" className="form-label">
                                    Host
                                </label>
                                <Field type="text" className="form-control" id="rule" name='hostName' disabled />
                                <ErrorMessage name='hostName' component={TextError} />
                            </div>

                            <div className="col-6 mb-3">
                                <label htmlFor="priceRegular" className="form-label">
                                    Price
                                </label>
                                <Field type="text" className="form-control" id="rule" name='priceRegular' />
                                <ErrorMessage name='priceRegular' component={TextError} />
                            </div>
                            <div className="col-12 mb-3">
                                <label htmlFor="busStation" className="form-label">
                                    Bus Station
                                </label>
                                <Field type="text" className="form-control" id="rule" name='busStation'  />
                                <ErrorMessage name='busStation' component={TextError} />
                            </div>
                            <div className="col-6 mb-3">
                                <label htmlFor="railwayStation" className="form-label">
                                    Railway Station
                                </label>
                                <Field type="text" className="form-control" id="rule" name='railwayStation'  />
                                <ErrorMessage name='railwayStation' component={TextError} />
                            </div>
                            <div className="col-6 mb-3">
                                <label htmlFor="airport" className="form-label">
                                    Airport
                                </label>
                                <Field type="text" className="form-control" id="rule" name='airport'  />
                                <ErrorMessage name='airport' component={TextError} />
                            </div>

                            <IncludeSelect includes={includes} />
                            <WhatToBringSelect carry={carry} />
                            <div className="col-md-6 mb-3">
                                <label htmlFor="experience_status" className="form-label">
                                    Status
                                </label>
                                <Field as='select' className='form-select' id="experience_status" name="experience_status" >
                                    <option  value={0}>Draft</option>
                                    <option  value={1}>Published</option>
                                    <option  value={1}>Closed</option>
                                  
                                </Field>
                                <ErrorMessage name="experience_status" component={TextError} />
                            </div>
                        </div>
                    </ModalBody>
                    <ModalFooter className='border-0 pt-0'>
                        <button
                            type="button"
                            className="btn btn-secondary btn-sm"
                            onClick={() => {
                                toggle();
                            }}
                        >
                            Close
                        </button>
                        <button type="submit" className="btn btn-primary btn-sm">
                            {isAddMode === "Add" ? `Save` : `Update`}
                        </button>
                    </ModalFooter>
                </Form>
                )}
            </Formik>
        </React.Fragment>
    );
};

export default EventsForm;