import React from 'react';
import { Field, ErrorMessage, useFormikContext } from 'formik';

const FacilitiesSelect = ({ facilities }) => {
  const { setFieldValue, values } = useFormikContext();

  const handleSelectChange = (event) => {
    const selectedValues = Array.from(event.target.selectedOptions, (option) => option.value);
    setFieldValue('includedFacilitiesFree', selectedValues); // Correctly update Formik's state with selected values
  };

  return (
    <div className="col-6 mb-3">
      <label htmlFor="includedFacilitiesFree" className="form-label">
        Facilities
      </label>
      <Field
        as="select"
        className="form-select"
        id="includedFacilitiesFree"
        name="includedFacilitiesFree"
        multiple
        value={values.includedFacilitiesFree} // Bind the value to Formik's state
        onChange={handleSelectChange}
      >
        <option disabled value="">
          Select facilities
        </option>
        {facilities.map((e, key) => (
          <option key={key} value={e._id}>
            {e.facilitiesName}
          </option>
        ))}
      </Field>
      <ErrorMessage name="includedFacilitiesFree" component="div" className="text-danger" />
    </div>
  );
};

export default FacilitiesSelect;
