import { ErrorMessage, Field, Form, Formik } from 'formik';
import React from 'react';
import { ModalBody, ModalHeader, ModalFooter } from "reactstrap";
import { toast } from 'react-toastify';
import TextError from '../../helpers/FormikHelper/TextError';
import userServices from '../../services/user.services';
import { FieldSchema } from '../../schema/FieldSchema';

const RulesForm = ({ toggle, isAddMode, selectedRow, getData }) => {
    // const [file, setFile] = useState(isAddMode === 'Add' ? '' : `${BASE_URL}/${selectedRow?.adventureImg}`);
    // const [expTypes, setExpTypes] = useState([])
    // function handleChange(e) {
    //     if (e.target.files.length > 0) {
    //         setFile(URL.createObjectURL(e.target.files[0]));
    //     } else {
    //         setFile('')
    //     }
    // }

    const initialValues = {
        rule: '',
        rulesType: ''
    }

    const savedValues = {
        rule: selectedRow?.rule,
        rulesType: selectedRow?.rulesType
    }

    const onSubmit = async (values) => {

        if (isAddMode === 'Add') {
            let result = await userServices.addRules(values);
            if (result.data.status === 'success') {
            
                toggle()
                getData()
                toast.success('Data Added successfully', { toastId: 'registration_success', autoClose: 3000 });
            } else {
                toast.error(result.data.message, { toastId: 'registration_fail', autoClose: 3000 });
            }
        } else {
            values.id = selectedRow?._id
            const result = await userServices.updateRules(values);
            if (result.data.status === 'success') {
         
                toggle()
                getData()
                toast.success('Data Updated successfully', { toastId: 'update_success', autoClose: 3000 });
            } else {
                toast.error(result.data.message, { toastId: 'update_fail', autoClose: 3000 });

            }
        }
    }


    return (
        <React.Fragment>
            <Formik
                initialValues={isAddMode === 'Add' ? initialValues : savedValues}
                validationSchema={FieldSchema(`${isAddMode === 'Add' ? 'ADD_RULES' : 'ADD_RULES'}`)}
                onSubmit={onSubmit}
                enableReinitialize
            >
                {({ setFieldValue, values }) => (<Form>
                    <ModalHeader toggle={toggle} className='border-0 pb-0'>
                        <div className="card-title d-flex align-items-center mb-0">
                            <h5 className="mb-0 text-primary">{isAddMode === 'Add' ? 'Add Rules' : 'Update Rules'}</h5>
                        </div>
                    </ModalHeader>
                    <ModalBody className="">
                        <div className="row" >
                            <div className="col-md-12 mb-3">
                                <label htmlFor="rulesType" className="form-label">
                                   Rules Type
                                </label>
                                <Field as='select' className='form-select' id="rulesType" name="rulesType" >
                                    <option disabled value="">Rules Type</option>
                                    <option value={1}>Easy</option>
                                    <option value={2}>Strict</option>
                                    <option value={3}>Eco</option>
                                   
                                </Field>
                                <ErrorMessage name="rulesType" component={TextError} />
                            </div>
                            <div className="col-12 mb-3">
                                <label htmlFor="rule" className="form-label">
                                    Rules
                                </label>
                                <Field type="text" className="form-control" id="rule" name='rule' />
                                <ErrorMessage name='rule' component={TextError} />
                            </div>
                            
                            
                        </div>
                    </ModalBody>
                    <ModalFooter className='border-0 pt-0'>
                        <button
                            type="button"
                            className="btn btn-secondary btn-sm"
                            onClick={() => {
                                toggle();
                            }}
                        >
                            Close
                        </button>
                        <button type="submit" className="btn btn-primary btn-sm">
                            {isAddMode === "Add" ? `Save` : `Update`}
                        </button>
                    </ModalFooter>
                </Form>
                )}
            </Formik>
        </React.Fragment>
    );
};

export default RulesForm;