import { useEffect, useState } from "react";
import userServices from "../services/user.services";
import { Modal } from 'reactstrap';
import ExperienceDetail from "../components/Experience/ExperienceDetailPage";
import ExperienceForm from "../components/Experience/ExperienceForm";
import ExperienceList from "../components/Experience/ExperienceList";

function Experience() {
    const [data, setData] = useState([])
    const [selectedRow, setSelectedRow] = useState();
    const [isAddMode, setIsAddMode] = useState('Add');
    const [modal, setModal] = useState(false);
    const [showDetail, setShowDetail] = useState(false)

    const toggle = () => setModal(!modal);
    const getData = async () => {
        const result = await userServices.expListForAdmin()
        if (result.status === 200) {
            let srNo = 1
            for (let i of result.data.data) {
                i.srNo = srNo
                srNo++;
            }
            setData(result.data.data)
        }
    }

    useEffect(() => {
        getData()
    }, [])
    return (
        <div className="page-inner">
            {/* Main Wrapper */}
            {showDetail === false ? <div id="main-wrapper">
                <div className="pageheader pd-t-25 pd-b-35">
                    <div className="pd-t-5 pd-b-5">
                        <h1 className="pd-0 mg-0 tx-24 text-overflow">EVENTS</h1>
                    </div>
                </div>

                <div className="row row-xs clearfix">
                    <div className="col-md-12 col-lg-12">
                        <div className="card mg-b-20">
                            <div className="card-header">
                                {/* <h4 className="card-header-title">Users List</h4> */}
                                <div className="card-header-btn">
                                    <a
                                        href
                                        data-toggle="collapse"
                                        className="btn card-collapse"
                                        data-target="#collapse1"
                                        aria-expanded="true"
                                    >
                                        <i className="ion-ios-arrow-down"></i>
                                    </a>


                                </div>

                                {/* <div className="btn-group">

                                    <button
                                        type="button "
                                        className="btn return-btn btn-sm"
                                    onClick={()=>{
                                        toggle();
                                        setIsAddMode('Add')
                                    }}
                                    >
                                        Add
                                    </button>
                                </div> */}
                            </div>

                            <div className="card-body collapse show" id="collapse1">

                                <ExperienceList
                                    data={data}
                                    setSelectedRow={setSelectedRow}
                                    setIsAddMode={setIsAddMode}
                                    setModal={setModal}
                                    setShowDetail={setShowDetail}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
                : <ExperienceDetail setShowDetail={setShowDetail} selectedRow={selectedRow} setSelectedRow={setSelectedRow} getData={getData}/>}
            <Modal isOpen={modal} toggle={toggle} size="md" centered={true} className="">
                <ExperienceForm toggle={toggle} isAddMode={isAddMode} selectedRow={selectedRow} getData={getData} />

            </Modal>

        </div>
    );
}

export default Experience;

